'use client';
import { useState, useEffect } from 'react';

interface Crisis {
  id: number;
  title: string;
  description: string;
  severity: string;
  affected_countries: string[];
  resolution_options: string[];
  active: boolean;
}

export default function CrisisManagement() {
  const [crises, setCrises] = useState<Crisis[]>([]);
  const [selectedResolution, setSelectedResolution] = useState('');
  const [result, setResult] = useState('');

  useEffect(() => {
    fetchActiveCrises();
  }, []);

  const fetchActiveCrises = async () => {
    try {
      const response = await fetch('http://localhost:8000/crisis/active');
      const data = await response.json();
      setCrises(data.crisis_events);
    } catch (error) {
      console.error('Error fetching crises:', error);
    }
  };

  const resolveCrisis = async (crisisId: number) => {
    if (!selectedResolution) return;

    try {
      const response = await fetch('http://localhost:8000/crisis/resolve', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          crisis_id: crisisId,
          resolution: selectedResolution,
          resources_committed: {}
        })
      });

      const data = await response.json();
      setResult(data.result);
      fetchActiveCrises(); // Refresh crises list
    } catch (error) {
      console.error('Error resolving crisis:', error);
      setResult('Failed to resolve crisis');
    }
  };

  const generateNewCrisis = async () => {
    try {
      const response = await fetch('http://localhost:8000/crisis/generate', {
        method: 'POST'
      });
      const data = await response.json();
      if (data.crisis) {
        setCrises(prev => [...prev, data.crisis]);
      }
    } catch (error) {
      console.error('Error generating crisis:', error);
    }
  };

  return (
    <div className="crisis-management">
      <h2>üö® Crisis Management Center</h2>
      
      <div className="crisis-controls">
        <button className="btn-generate-crisis" onClick={generateNewCrisis}>
          Simulate New Crisis
        </button>
      </div>

      <div className="crises-grid">
        {crises.length === 0 ? (
          <div className="no-crises">
            <p>No active crises. The nation is at peace.</p>
          </div>
        ) : (
          crises.map(crisis => (
            <div key={crisis.id} className={`crisis-card ${crisis.severity}`}>
              <h3>{crisis.title} 
                <span className={`severity-badge ${crisis.severity}`}>
                  {crisis.severity.toUpperCase()}
                </span>
              </h3>
              <p>{crisis.description}</p>
              
              <div className="resolution-options">
                <h4>Resolution Options:</h4>
                <select 
                  value={selectedResolution} 
                  onChange={(e) => setSelectedResolution(e.target.value)}
                >
                  <option value="">Select resolution</option>
                  {crisis.resolution_options.map(option => (
                    <option key={option} value={option}>
                      {option.replace(/_/g, ' ')}
                    </option>
                  ))}
                </select>
                
                <button 
                  className="btn-resolve"
                  onClick={() => resolveCrisis(crisis.id)}
                  disabled={!selectedResolution}
                >
                  Resolve Crisis
                </button>
              </div>
            </div>
          ))
        )}
      </div>

      {result && (
        <div className="resolution-result">
          <h3>Crisis Resolution Result</h3>
          <p>{result}</p>
        </div>
      )}
    </div>
  );
}